import React from "react";
import styled from "styled-components";
import useAxios from "../api/Axios";
import {
  createSearchParams,
  useLocation,
  useNavigate,
  useParams,
} from "react-router-dom";
import { useAuth } from "../Context/Auth";
const ClassInformation = React.lazy(() =>
  import("../components/assets/ClassInformation")
);

const Dashboard = () => {
  const axios = useAxios();
  const navigate = useNavigate();
  const location = useLocation();
  const auth = useAuth();
  const [classInfo, setClassInfo] = React.useState([]);

  const param = useParams();

  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    setLoading(true);
    auth.handleDashboard(true);
    auth.handleSetTitle("Class Assesment Marks");
    const getClass = async () => {
      const url = "/all?id=" + param.tid;
      await axios.get(url).then((res) => {
        setClassInfo(res.data);
      });
    };
    getClass();
    setLoading(false);
  }, [auth, auth.user, axios, param.tid, location.pathname]);

  const handleClickedClass = (id) => {
    const path = location.pathname.replace("/dashboard", "/class/view");
    navigate({
      pathname: path,
      search: createSearchParams({
        id: id,
      }).toString(),
    });
  };
  return (
    <Section>
      {loading ? (
        "Loading..."
      ) : (
        <Container>
          <ClassPanel>
            {classInfo.length > 0 ? (
              classInfo.map((data) => {
                return (
                  <Classes
                    key={data.id}
                    onClick={() => handleClickedClass(data.classId)}
                  >
                    <ClassInformation classInfo={data} />
                  </Classes>
                );
              })
            ) : (
              <Panel></Panel>
            )}
          </ClassPanel>
        </Container>
      )}
    </Section>
  );
};

export default Dashboard;

const Section = styled.section`
  margin-top: 5px;
  width: 97.7vw;
  height: 90vh;
  overflow-y: visible;
`;

const Container = styled.div`
  padding: 0.5rem;
`;

const Classes = styled.div`
  cursor: pointer;
  margin-bottom: 0.5rem;
  width: 491px;
  margin-right: 5px;
  margin-bottom: 5px;

  @media (max-width: 992px) {
    width: 100%;
    &:nth-child(2n + 1) {
      margin-right: 0px;
    }
    &:nth-child(2n + 2) {
      margin-left: 0px;
    }
  }
`;

const Panel = styled.div`
  height: 83.5vh;
  background: blue;
`;

const ClassPanel = styled.div`
  display: grid;
  grid-template-columns: auto auto auto;

  @media (max-width: 992px) {
    grid-template-columns: auto;
  }
`;
